<!---->
<template>
  <div class="movieinfo">
    <div class="overall">
      <!---->
      <div class="desc">
        <h2 class="title">{{moviedetail.title}}</h2>
        <span class="original-title">{{moviedetail.original_title}}</span>
        <span class="duration">{{durations}}</span>
        <span class="tag">{{tag}}</span>
        <span class="pubdate">{{pubdate}}</span>
      </div>
      <!---->
      <div class="rank">
        <span class="origin"></span>
        <span class="rating" v-show="rating">{{normalizeScore()}}</span>
        <star :size="24" :score="moviedetail.rating.average" :needNullStar="needNullStar" class="star"></star>
        <span class="num">{{moviedetail.ratings_count}} </span>
      </div>
    </div>
    <!---->
    <div class="operate">
      <!---->
      <div class="want-watch"
           v-text="wantedText"
           @click="saveWantedMovie"
           :class="{'wanted':isWanted(moviedetail.id)}"
      ></div>
      <!---->
      <div class="has-watched"
           @click="saveWatchedMovie"
           :class="{'wacthed':isWatched(moviedetail.id)}"
      ><img src="./avatar.jpg" alt="" width="25" height="25" v-show="hasWacthed">
        {{watchedText}}
      </div>
    </div>
    <!---->
    <div class="summary">
      <h2 class="title"></h2>
      <p class="content">{{moviedetail.summary}}</p>
    </div>
    <h2 class="casts-content-title"></h2>
    <scroll class="casts" :scrollX="this.scrollX" :eventPassthrough="this.eventPassthrough" ref="scroll" :click="this.click">
      <div class="casts-content" ref="content">
        <div class="cast-item" v-for="item in allCasts" @click="selectCelebrity(item.id,$event)">
          <img v-lazy="replaceUrl(item.avatars.large)" alt="" width="90" height="120">
          <h3 class="item-title">{{item.name}}</h3>
          <span v-if="item.isDirector"></span>
          <span v-else></span>
        </div>
        <div class="no-result" v-if="!allCasts.length">...</div>
      </div>
    </scroll>
  </div>
</template>

<script type="text/ecmascript-6">
  import scroll from 'base/scroll/scroll.vue'
  import star from 'base/star/star.vue'
  import {mapGetters, mapActions} from 'vuex'

  export default {
    data() {
      return {
        eventPassthrough: 'vertical',           // better-scroll
        scrollX: true,
        needNullStar: true,          // 
        wantedText: '',
        watchedText: ' ',
        wanted: false,                     // 
        hasWacthed: false,                 // 
        click: false
      }
    },
    props: {
      moviedetail: {
        type: Object,
        default: {}
      }
    },
    mounted() {
      this.$nextTick(() => {
        this._initPic()
        this.$refs.scroll.refresh()
      })
    },
    computed: {
      ...mapGetters(['movie', 'watchedMovies', 'wantedMovies']),
      tag() {              // 
        let year = this.moviedetail.year
        let tag = this.moviedetail.genres.join(' / ')            // join() 
        return `${year} / ${tag}`
      },
      pubdate() {            // 
        let pubdate = ''
        let date = this.moviedetail.pubdates
        for (let i = 0; i < date.length; i++) {
          if (date[i].indexOf('') === -1) {          // indexOf() -1
            pubdate = date[i]           // 
          }
          if (date[i].indexOf('') > -1) {       // 
            pubdate = date[i]
            break           // 
          }
        }
        if (!pubdate) {          // 
          pubdate = ''
        }
        return pubdate
      },
      durations() {            // 
        return this.moviedetail.durations[0]
      },
      rating() {               // 
        let rating = this.moviedetail.rating.average
        if (rating === 0) {
          return false
        }
        return true
      },
      allCasts() {             // 
        let removeIndex = []
        this.moviedetail.directors.forEach((item, index) => {
          item.isDirector = true
          if (item.avatars === null) {        // 
            removeIndex.push(index)
          }
        })
        for (let i = removeIndex.length; i > 0; i--) {
          this.moviedetail.directors.splice(removeIndex[i - 1], 1)
        }
        removeIndex = []       // 
        this.moviedetail.casts.forEach((item, index) => {
          if (item.avatars === null) {        // 
            removeIndex.push(index)
          }
        })
        for (let i = removeIndex.length; i > 0; i--) {
          this.moviedetail.casts.splice(removeIndex[i - 1], 1)
        }
        return this.moviedetail.directors.concat(this.moviedetail.casts)
      }
    },
    methods: {
      ...mapActions(['markWatchedMovie', 'markWantedMovie']),
      normalizeScore() {          // 
        let len = this.moviedetail.rating.average.toString().length                    // toString() 
        if (len < 2) {
          return `${this.moviedetail.rating.average}.0`
        }
        return this.moviedetail.rating.average
      },
      saveWantedMovie() {               // actions
        this.markWantedMovie(this.movie)
        const index = this.wantedMovies.findIndex((item) => {
          return item.id === this.movie.id
        })
        if (index > -1) {         // 
          this.wantedText = ''
        } else {
          this.wantedText = ''
        }
      },
      isWanted(id) {                 // 
        const index = this.wantedMovies.findIndex((item) => {
          return item.id === id
        })
        if (index > -1) {
          return true
        }
        return false
      },
      saveWatchedMovie() {         // actions
        this.markWatchedMovie(this.movie)
        const index = this.watchedMovies.findIndex((item) => {
          return item.id === this.movie.id
        })
        if (index > -1) {            // 
          this.hasWacthed = true
          this.watchedText = ''
        } else {
          this.hasWacthed = false
          this.watchedText = ' '
        }
      },
      isWatched(id) {                 // 
        const index = this.watchedMovies.findIndex((item) => {
          return item.id === id
        })
        if (index > -1) {
          this.hasWacthed = true
          return true
        }
        return false
      },
      _initPic() {             // 
        let picWidth = 90
        let margin = 8
        let width = (picWidth + margin) * this.allCasts.length - margin
        this.$refs.content.style.width = width + 'px'
      },
      selectCelebrity(id) {               // 
        if (!event._constructed) {
          return;
        }
        this.$emit('selectCelebrity', id)
      },
      replaceUrl(url) {            // ,
        return ('https://images.weserv.nl/?url=' + url.replace(/http\w{0,1}:\/\//, ''))
      }
    },
    components: {
      scroll,
      star
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" type="text/stylus" scoped>
  @import "~@/common/stylus/mixin.styl"

  .movieinfo {
    background-color: #fff
    padding 0 20px
  }

  .movieinfo .overall {
    display: flex
    height: 140px
    justify-content space-between
  }

  .overall .desc {
    flex 70% 0 0
    overflow: hidden
    display flex
    flex-direction column
    justify-content space-around
  }

  .desc .title {
    font-size 18px
    color #333
    padding 10px 0 5px 0
    no-wrap()
  }

  .desc span {
    font-size 12px
    no-wrap()
  }

  .overall .rank {
    height: 70px
    margin-top: 15px
    padding 10px 8px
    display: flex
    align-items center
    flex-direction column
    justify-content space-between
    box-shadow 0px 0px 5px #ccc
    background-color: #f8f8f8
  }

  .rank .star {
    text-align: center
  }

  .rank span {
    font-size 12px
    color #777
  }

  .rank .rating {
    font-size 18px
    color #333
  }

  .rank .star {
    display: block
  }

  .summary {
    margin-top 20px
    padding-top: 20px
    boeder-top-1px(#ccc)
  }

  .summary .title {
    font-size 14px
    color #000
  }

  .summary .content {
    text-indent 1.5em
    padding-top 10px
    font-size 14px
    color #333
    line-height: 25px
  }

  .operate {
    margin-top: 20px
    line-height: 35px
    height: 35px
    display flex
    text-align center
    font-size 14px
    color #42bd56
  }

  .operate .want-watch {
    flex 1
    border 1px solid #42bd56
    border-radius 5px
    margin-right 20px
  }

  .want-watch.wanted {
    border: 1px solid #FE9800
    color #FE9800
  }

  .operate .has-watched {
    flex 2
    border 1px solid #42bd56
    border-radius 5px
  }

  .has-watched.wacthed {
    border: 1px solid #FE9800
    color #FE9800
  }

  .has-watched img {
    display inline-block
    vertical-align middle
    padding-bottom 2px
    border-radius 50%
  }

  .casts {
    padding 15px 0 30px 0
    width: 100%
    white-space nowrap
    overflow: hidden
    font-size 0
    border-bottom-1px(#ccc)
  }

  .casts-content-title {
    font-size 14px
    padding-top 30px
  }

  .casts-content .cast-item {
    width: 90px
    vertical-align top
    display inline-block
    text-align: center
    margin-right 8px
    font-size 12px
  }

  .cast-item img {
    height: 120px
    width: 90px
  }

  .cast-item .item-title {
    color #333
    padding 8px 0 5px 0
    no-wrap()
  }

  .no-result {
    text-align center
    font-size 12px
    color #42bd56
  }
</style>



