<!---->
<template>
  <transition name="search-move">
    <div class="search">
      <!---->
      <searchBox @query="onQueryChange"
                 :tagSearch="tagSearch"
                 v-show="!tagSearch"
                 ref="searchBox"
                 @focusInput="focusInputs"
                 @blurInput="blurInputs"
      ></searchBox>
      <div class="fixed-title" v-show="tagSearch">
        <span class="back" @click="back">
          <i class="icon-back"></i>
        </span>
        <span class="type">
          <span>{{query}}</span>
        </span>
      </div>
      <div class="shortcut-wrapper" v-show="!query">
        <div class="tag-list">
          <h2 class="search-title"></h2>
          <div class="douban-tag">
            <span class="tag-item" v-for="item in doubanTag" @click="tagQuery(item)">{{item}}</span>
          </div>
        </div>
        <!---->
        <div class="search-history">
          <h1 class="title">
            <span class="text"></span>
            <span class="clear" @click="showConfirm">
                <i class="icon-bin"></i>
              </span>
          </h1>
          <scroll class="shortcut" ref="shortcut" :beforeScroll="beforeScroll" @beforeScroll="listenScroll">
            <div>
              <historylist :searches="searchHistory" @select="addQuery" @delete="deleteOne"></historylist>
            </div>
          </scroll>
        </div>
      </div>
      <div class="search-result" v-if="query" ref="searchResult" :class="{'tag-search':tagSearch}">
        <suggest ref="suggest"
                 :tagSearch="tagSearch"
                 :query="query"
                 @listScroll="blurInput"
                 @select="saveSearch"
        ></suggest>
      </div>
      <confirm ref="confirm" text="" confirmbtnText="" @confirm="clearSearchHistory"></confirm>
      <tab :tabFlag="tabFlag"></tab>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import searchBox from 'base/searchBox/searchBox.vue'
  import scroll from 'base/scroll/scroll.vue'
  import confirm from 'base/confirm/confirm.vue'
  import suggest from 'components/suggest/suggest.vue'
  import historylist from 'base/historylist/historylist.vue'
  import {mapGetters, mapActions} from 'vuex'
  import tab from 'components/tab/tab.vue'

  export default {
    name: 'search',
    data() {
      return {
        query: '',
        tagSearch: false,
        doubanTag: ['', '', '', '', '', '', '', '', '', '', ''],
        beforeScroll: true,
        tabFlag: true
      }
    },
    watch: {
      query(newQuery) {
        if (!newQuery) {
          setTimeout(() => {
            this.$refs.shortcut.refresh()
          }, 20)
        }
      }
    },
    computed: {
      ...mapGetters(['searchHistory'])
    },
    mounted() {
      this.$nextTick(() => {
        this.focusInput()
      })
    },
    methods: {
      ...mapActions(['saveSearchHistory', 'deleteSearchHistory', 'clearSearchHistory']),
      onQueryChange(query) {           // query
        this.query = query
      },
      back() {                 // 
        this.tagSearch = false
        this.query = ''
      },
      tagQuery(query) {          // 
        this.tagSearch = true
        this.query = query
      },
      listenScroll() {          // 
        this.blurInput()
      },
      blurInput() {           // 
        this.$refs.searchBox.blur()
      },
      focusInput() {         // 
        this.$refs.searchBox.focus()
      },
      showConfirm() {        // 
        this.$refs.confirm.show()
      },
      saveSearch() {     // 
        if (!this.tagSearch) {
          this.saveSearchHistory(this.query)
        }
      },
      addQuery(query) {              // 
        this.$refs.searchBox.setQuery(query)
      },
      deleteOne(item) {
        this.deleteSearchHistory(item)
      },
      focusInputs() {
        this.tabFlag = false
      },
      blurInputs() {
        this.tabFlag = true
      }
    },
    components: {
      searchBox,
      scroll,
      confirm,
      suggest,
      historylist,
      tab
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" type="text/stylus" scoped>
  @import "~@/common/stylus/mixin.styl"

  .search {
    position: fixed
    top: 0
    left: 0
    bottom: 0
    right: 0
    background-color: #fff
    z-index 9995
  }

  .search-move-enter-active, .search-move-leave-active {
    transition all 0.3s
  }

  .search-move-enter, .search-move-leave-to {
    transform translate3d(-100%, 0, 0)
  }

  .fixed-title {
    border-bottom-1px(#ccc)
    position: fixed
    top: 0
    width: 100%
    height: 50px
    /*z-index: 300*/
    /*background-color: #fff*/
  }

  .fixed-title .back {
    position: absolute
    top: 0px
    left: 6px
    z-index: 100
  }

  .back .icon-back {
    display: block
    padding: 15px
    font-size 18px
    color #42bd56
  }

  .fixed-title .type {
    line-height: 50px
    font-size 16px
    padding-left 60px
    color #333
  }

  .shortcut-wrapper {
    position: fixed
    top: 50px
    bottom: 0
    width: 100%
  }

  .shortcut-wrapper .shortcut {
    position: absolute;
    top: 230px;
    bottom: 75px
    overflow: hidden;
    box-sizing: border-box;
    width: 100%
  }

  .tag-list {
    padding 15px 15px 0 15px
  }

  .tag-list .search-title {
    color #98989A
    font-size 16px
    padding-bottom 15px
  }

  .douban-tag .tag-item {
    display: inline-block;
    padding: 5px 10px;
    margin: 0 20px 10px 0;
    border-radius: 6px;
    font-size 14px
    color #42bd56
    border 1px solid #42bd56
  }

  .search-history .title {
    display flex
    align-items top
    height: 40px
    font-size 18px
    color #42bd56
    padding 20px 20px 0 20px
  }

  .title .text {
    flex 1
  }

  .title .clear {
    extend-click()
  }

  .clear .icon-bin {
    font-size 18px
    color #42bd56
  }

  .search-result {
    position fixed
    width: 100%
    top: 50px
    bottom: 0
    background-color: #fff;
  }
</style>



