<!---->
<template>
  <div class="allcomments">
    <div class="fixed-title">
      <span class="back" @click="back">
        <i class="icon-back"></i>
      </span>
      <span class="type">{{title}}</span>
      <span class="to-top" @click="toTop">
        <i class="icon-circle-up"></i>
      </span>
    </div>
    <scroll class="all-discuss"
            ref="scroll"
            :data="discussion"
            @scrollToEnd="loadMore"
            :pullup="pullup"
    >
      <div class="discuss-content">
        <!---->
        <moviecomment v-if="reviewsType === 'comments'"
                      :needTitle="needTitle"
                      :comments="discussion"
                      :commentNum="discussionNum"
                      :hasMore="hasMore"
        ></moviecomment>
        <!---->
        <moviereview v-else
                     :reviews="discussion"
                     :reviewsNum="discussionNum"
                     :needTitle="needTitle"
                     :hasMore="hasMore"
                     @selectReview="selectReview"
        ></moviereview>
      </div>
      <loading v-if="!discussion.length && !noResult" :fullScreen="fullScreen"></loading>
      <div class="no-result" v-if="noResult">...</div>
    </scroll>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import {mapGetters, mapMutations} from 'vuex'
  import scroll from 'base/scroll/scroll.vue'
  import loading from 'base/loading/loading.vue'
  import {getAllComments, getAllReviews} from 'api/moviedetail.js'
  import moviecomment from 'components/moviecomment/moviecomment.vue'
  import moviereview from 'components/moviereview/moviereview.vue'

  const SEARCH_MORE = 20            // 

  export default {
    data() {
      return {
        discussionIndex: 0,        // 
        discussion: [],
        discussionNum: 0,
        pullup: true,            // 
        hasMore: true,           // 
        noResult: false
      }
    },
    created() {
      this.fullScreen = true             // 
      this.needTitle = true
      this.loadingFlag = true            // 
      this._getAlldiscussion()
    },
    computed: {
      ...mapGetters(['movie', 'reviewsType']),           // 
      title() {                                 // 
        return `${this.movie.title}${this.type}`
      },
      type() {
        return this.reviewsType === 'comments' ? '' : ''
      }
    },
    methods: {
      ...mapMutations({setReview: 'SET_REVIEWS_ID'}),            // IDMutation
      toTop() {                                    // 
        this.$refs.scroll.scrollTo(0, 0, 500)
      },
      back() {                     // 
        this.$router.back()
      },
      _getAlldiscussion() {                     // 
        if (!this.reviewsType) {               // 
          this.$router.push({
            path: `/moiveshow/${this.movie.id}`
          })
        }
        let movieType = this.reviewsType === 'comments'
        this.discussType = movieType ? 'comments' : 'reviews'
        this.getData = movieType ? getAllComments : getAllReviews
        this.getData(this.movie.id, this.discussionIndex, SEARCH_MORE).then((res) => {
          this.discussion = res[this.discussType]
          this.noResult = this.discussion.length ? this.noResult : true              // 
          this.discussionNum = res.total
          this._checkMore(res)
          // console.log(res)
        })
      },
      _checkMore(data) {              // 
        const discussion = data.comments ? data.comments : data.reviews
        if (!discussion.length || data.start + data.count > data.total) {
          this.hasMore = false
        }
      },
      loadMore() {           // 
        if (!this.loadingFlag || !this.hasMore) {             // 
          return
        }
        this.loadingFlag = false             // 
        this.discussionIndex += SEARCH_MORE
        this.getData(this.movie.id, this.discussionIndex, SEARCH_MORE).then((res) => {
          this.discussion = this.discussion.concat(res[this.discussType])
          this._checkMore(res)
          this.loadingFlag = true
        })
      },
      selectReview(id) {                   // 
        this.setReview(id)
        this.$router.push({
          path: `/moiveshow/${this.movie.id}/reviews/${id}`
        })
      }
    },
    components: {
      scroll,
      moviecomment,
      loading,
      moviereview
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" type="text/stylus" scoped>
  @import "~@/common/stylus/mixin.styl"

  .allcomments {
    height: 100%
  }

  .allcomments .fixed-title {
    border-bottom-1px(#ccc)
    position fixed
    top: 0
    width: 100%
    height: 50px
    z-index 500
    background-color: #fff
  }

  .fixed-title .back {
    position absolute
    top: 0
    left: 6px
    z-index 150
  }

  .back .icon-back {
    display block
    padding 15px
    font-size 18px
    color #42bd56
  }

  .fixed-title .type {
    line-height: 50px
    font-size 16px
    padding-left 50px
  }

  .fixed-title .to-top {
    position: absolute
    top: 0
    right 6px
    z-index 150
  }

  .to-top .icon-circle-up {
    display block
    padding 15px
    font-size 22px
    color #42bd56
  }

  .all-discuss {
    position: fixed
    top: 0
    bottom: 0
    right: 0
    left: 0
    z-index 250
    overflow hidden
    background-color: #fff
  }

  .all-discuss .discuss-content {
    padding-top: 50px
  }

  .no-result {
    position: absolute
    top: 50%
    width: 100%
    text-align center
    font-size 17px
    color: #0AAF0A
  }
</style>



