<template>
  <div class="moiveshow">
    <!---->
    <search @search="goSearch"></search>
    <!---->
    <switches :switches="switches" :currentIndex="currentIndex" @switch="switchItem"></switches>
    <!---->
    <div class="list-wrapper">
      <!---->
      <scroll v-show="currentIndex===0"
              class="list-scroll"
              :data="moviehot"
              ref="moviehot"
              :pullup="pullup"
              @scrollToEnd="loadMore"
      >
        <div class="list-inner">
          <!---->
          <movielist :moviehot="moviehot" :hasMore="hotData" @select="selectMovie"></movielist>
        </div>
      </scroll>
      <!---->
      <scroll v-show="currentIndex===1"
              class="list-scroll"
              :data="movieshow"
              ref="movieshow"
              :pullup="pullup"
              @scrollToEnd="loadMore"
              :probeType="probeType"
              :listenScroll="listenScroll"
              @scroll="scroll"
      >
        <div class="list-inner">
          <!---->
          <movielist :moviehot="movieshow"
                     :hasMore="showData"
                     ref="list"
                     :timeData="timeData"
                     @getHeight="getHeight"
                     @getMap="getMap"
                     @select="selectMovie"
          >
          </movielist>
        </div>
      </scroll>
      <loading
        :fullScreen="fullScreen"
        v-show="currentIndex===1 && !movieshow.length || currentIndex===0 && !moviehot.length">
      </loading>
    </div>
    <div class="list-fixed" ref="fixed" v-show="currentIndex===1" v-if="fixedTitle">
      <h1 class="fixed-title">{{fixedTitle}}</h1>
    </div>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import {getMovieShow, getComingMovie} from 'api/movieShow.js'
  import {ERR_OK} from 'api/config'
  import switches from 'base/switches/switches.vue'
  import scroll from 'base/scroll/scroll.vue'
  import {createMovieList} from 'common/js/movielist.js'
  import movielist from 'components/movielist/movielist.vue'
  import loading from 'base/loading/loading.vue'
  import search from 'base/search/search.vue'
  import {mapMutations} from 'vuex'

  const SEARCH_MORE = 10         // 
  const TITLE_HEIGHT = 30       // 

  export default {
    data() {
      return {
        moviehot: [],              // 
        movieshow: [],            // 
        currentIndex: 0,
        hotData: true,          // 
        showData: true,        // 
        hotDataIndex: 0,      // 
        showDataIndex: 0,    // 
        switches: [
          {name: ''},
          {name: ''}
        ],
        pullup: true,           // 
        timeData: true,         // 
        loadingFlag: true,      // 
        scrollY: -1,
        diff: -1,
        fullScreen: true,        // 
        scrollIndex: 0
      }
    },
    created() {
      this._getMovieShow()            // 
      this.probeType = 3             // better-scroll
      this.listenScroll = true       // better-scroll
      this.listHeight = []           // 
      this.scrollMap = []            // 
    },
    computed: {
      fixedTitle() {
        if (this.scrollY > 0) {
          return ''
        }
        return this.scrollMap[this.scrollIndex] ? this.scrollMap[this.scrollIndex] : ''
      }
    },
    methods: {
      ...mapMutations({
        setMovie: 'SET_MOVIE'
      }),
      selectMovie(item) {         // vuexMutations
        this.$router.push({
          path: `/moiveshow/${item.id}`
        })
        this.setMovie(item)
        //  console.log(item)
      },
      goSearch() {            // 
        this.$router.push({
          path: `/search`
        })
      },
      _getMovieShow() {         // 
        getMovieShow(this.hotDataIndex, SEARCH_MORE).then((res) => {
          if (res.start === ERR_OK) {
            this.$emit('hasLoad')                   // hasLoadAPP.vue
            this.moviehot = createMovieList(res.subjects)
            this._checkMore(res)          // _checkMore
            // console.log(this.moviehot)
          }
        })
      },
      switchItem(index) {              // index
        this.currentIndex = index
        if (index === 1) {              // 
          this.$refs.list.recalculate()
          this.$refs.movieshow.scrollTo(0, this.scrollY)     // 
        }
        setTimeout(() => {                // scroll
          this.$refs.moviehot.refresh()
          this.$refs.movieshow.refresh()
        }, 20)
        if (index === 1 && !this.movieshow.length) {        // 
          getComingMovie(this.showDataIndex, SEARCH_MORE).then((res) => {
            this.movieshow = createMovieList(res.subjects)
            this._checkMore(res)
          })
        }
      },
      loadMore() {       // 
        if (!this.loadingFlag) {          // 
          return
        }
        this.loadingFlag = false         // 
        if (this.currentIndex === 0) {    // 
          if (!this.hotData) {        // 
            this.loadingFlag = true
            return
          }
          this.hotDataIndex += SEARCH_MORE
          getMovieShow(this.hotDataIndex, SEARCH_MORE).then((res) => {
            this.moviehot = this.moviehot.concat(createMovieList(res.subjects))        // concat() 
            this._checkMore(res)          // _checkMore
            this.loadingFlag = true
          })
        } else {
          if (!this.showData) {          // 
            this.loadingFlag = true
            return
          }
          this.showDataIndex += SEARCH_MORE
          getComingMovie(this.showDataIndex, SEARCH_MORE).then((res) => {
            this.movieshow = this.movieshow.concat(createMovieList(res.subjects))
            this._checkMore(res)
            this.loadingFlag = true
          })
        }
      },
      _checkMore(data) {          // 
        const movies = data.subjects
        if (!movies.length || data.start + data.count >= data.total) {
          if (this.currentIndex === 0) {
            this.hotData = false
          } else {
            this.showData = false
          }
          this.loadingFlag = true
        }
      },
      scroll(pos) {           // 
        this.scrollY = pos.y
      },
      getHeight(height) {               // 
        this.listHeight = height
        // console.log(height)
      },
      getMap(map) {              // 
        this.scrollMap = map
        // console.log(map)
      }
    },
    watch: {
      scrollY(newY, oldY) {
        if (!newY) {             // tab
          this.scrollY = oldY
        }
        if (this.listHeight.length === 0 || this.scrollMap.length === 0) {           // 
          return
        }
        if (newY > 0) {          // newY>0
          this.scrollIndex = 0
          return
        }
        // 
        for (let i = 0; i < this.listHeight.length; i++) {
          let height1 = this.listHeight[i]
          let height2 = this.listHeight[i + 1]
          if (-newY >= height1 && -newY < height2) {
            this.scrollIndex = i
            this.diff = height2 + newY
            return
          }
        }
        // newY
        this.scrollIndex = this.listHeight.length - 2
      },
      diff(newVal) {
        let fixedtop = (newVal > 0 && newVal < TITLE_HEIGHT) ? newVal - TITLE_HEIGHT : 0
        if (this.fixedtop === fixedtop) {
          return
        }
        this.fixedtop = fixedtop
        this.$nextTick(() => {
          this.$refs.fixed.style.transform = `translate3d(0,${fixedtop}px,0)`
        })
      }
    },
    components: {
      switches,
      scroll,
      movielist,
      loading,
      search
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" type="text/stylus" scoped>
  .moiveshow {
    height: 100%
  }

  .list-wrapper {
    position: absolute
    top: 102px
    bottom: 61px
    width: 100%
  }

  .list-wrapper .list-scroll {
    position: relative
    height: 100%
    overflow: hidden
  }

  .list-wrapper .list-inner {
    padding 0px 15px
  }

  .list-fixed {
    position: absolute
    top: 102px
    right: 15px
    left: 15px
  }

  .list-fixed .fixed-title {
    width: 100%
    padding-left: 5px
    height: 30px
    line-height: 30px
    background-color: #eee
  }

</style>



