import {loadWatchedMovie, loadWantedMovie, loadComment, loadSearch, loadCelebrity} from '../common/js/cache.js'

const state = {
  movie: {},                                            // 
  watchedMovies: loadWatchedMovie(),                   // 
  wantedMovies: loadWantedMovie(),                    // 
  reviewsType: '',                                   // 
  reviewId: 0,                                      // ID
  favoriteComments: loadComment(),                 // 
  searchHistory: loadSearch(),                    // 
  currentCelebrityId: 0,                         // ID
  collected: loadCelebrity()                    // 
}

export default state



