<template>
  <transition name="fade">
    <div class="moviedetail">
      <div class="fixed-title">
        <span class="back" @click="back">
          <i class="icon-back"></i>
        </span>
        <span class="type" ref="fixed">
          <span class="icon" v-show="!this.changeFix">
            <i class="icon-video-camera"></i>
          </span>
          <span class="movie-name" v-if="changeFix" v-text="moviedetail.title"></span>
        </span>
      </div>
      <scroll class="movie-detail"
              :data="moviedetail"
              :probeType="probeType"
              :listenScroll="listenScroll"
              ref="scroll"
              @scroll="scroll"
      >
        <div class="scroll-wrapper">
          <div class="scroll-content" v-if="moviedetail.images">
            <div class="bg-image" ref="image">
              <img v-lazy="replaceUrl(moviedetail.images.large)" alt="">
            </div>
            <!---->
            <movieinfo :moviedetail="moviedetail" @selectCelebrity="selectCelebrity"></movieinfo>
            <div class="switch">
              <!--tab-->
              <switches :switches="switches" :currentIndex="currentIndex" @switch="selectItem"></switches>
            </div>
            <!---->
            <moviecomment v-show="currentIndex===0"
                          :comments="moviedetail.popular_comments"
                          :commentNum="moviedetail.comments_count"
                          @needAllComments="needAllComments"
            ></moviecomment>
            <!---->
            <moviereview v-show="currentIndex===1"
                         :reviews="moviedetail.popular_reviews.slice(0,5)"
                         :reviewsNum="moviedetail.reviews_count"
                         @needAllReviews="needAllReviews"
                         @selectReview="selectReview"
            ></moviereview>
          </div>
        </div>
        <loading :fullScreen="fullScreen" v-show="!moviedetail.images"></loading>
      </scroll>
      <router-view></router-view>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import scroll from 'base/scroll/scroll.vue'
  import switches from 'base/switches/switches.vue'
  import {getMoviedetail} from 'api/moviedetail.js'
  import movieinfo from 'components/movieinfo/movieinfo.vue'
  import moviecomment from 'components/moviecomment/moviecomment.vue'
  import moviereview from 'components/moviereview/moviereview.vue'
  import {mapGetters, mapMutations} from 'vuex'
  import loading from 'base/loading/loading.vue'

  export default {
    name: 'moviedetail',            // namekeep-alive
    data() {
      return {
        moviedetail: {},
        fullScreen: true,          // loading
        switches: [
          {name: ''},
          {name: ''}
        ],
        currentIndex: 0,
        scrollY: -1,
        imgHeight: -1,
        changeFix: false             // fix
      }
    },
    created() {
      this._getMoviedetail()
      this.probeType = 3          // scrollscrollswipe
      this.listenScroll = true    // scrollscroll
    },
    computed: {
      ...mapGetters(['movie'])                 // vuex
    },
    methods: {
      ...mapMutations({                       // vuex
        setMovieType: 'SET_REVIEWS_TYPE',
        setReview: 'SET_REVIEWS_ID',
        setCelebrity: 'SET_CELEBRITY_ID'
      }),
      back() {                 // 
        this.$router.back()
      },
      _getMoviedetail() {                 // 
        if (!this.movie.id) {             // 
          this.$router.push('/moiveshow')
          return
        }
        getMoviedetail(this.movie.id).then((res) => {
          this.moviedetail = res
          // console.log(res)
        })
      },
      selectItem(index) {             // tab
        this.currentIndex = index
        this.$nextTick(() => {
          this.$refs.scroll.refresh()
        })
      },
      needAllComments() {                 // 
        this.setMovieType('comments')       // commentsvuexmutation
        this.$router.push({
          path: `/moiveshow/${this.moviedetail.id}/comments`
        })
      },
      needAllReviews() {                // 
        this.setMovieType('reviews')       // reviewsvuexmutation
        this.$router.push({
          path: `/moiveshow/${this.moviedetail.id}/reviews`
        })
      },
      selectReview(id) {                   // 
        this.setReview(id)
        this.$router.push({
          path: `/moiveshow/${this.movie.id}/reviews/${id}`
        })
      },
      scroll(pos) {
        this.scrollY = pos.y
        if (-this.imgHeight > pos.y && this.imgHeight !== -1) {
          this.changeFix = true                 // fixed
        } else {
          this.changeFix = false
        }
      },
      selectCelebrity(id) {
        this.setCelebrity(id)
        this.$router.push({
          path: `/clebrity/${id}`
        })
      },
      replaceUrl(url) {            // ,
        return ('https://images.weserv.nl/?url=' + url.replace(/http\w{0,1}:\/\//, ''))
      }
    },
    components: {
      scroll,
      movieinfo,
      switches,
      moviecomment,
      moviereview,
      loading
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" type="text/stylus" scoped>
  .moviedetail {
    position: fixed
    top: 0
    left: 0
    right: 0
    bottom: 0
    background-color: #fff
    z-index 222
  }

  .fade-enter-active {
    animation move 0.3s
  }

  /*.fade-leave-active {
    transition all 0.3s
  }*/

  .fade-leave-to {
    transition all 0.3s
    transform translate3d(-100%, 0, 0)
  }

  @keyframes move {
    0% {
      opacity 0
      transform scale(0.3)
    }

    100% {
      opacity 1
      transform scale(1)
    }
  }

  .fixed-title {
    position fixed
    top: 0
    width: 100%
    height: 40px
    line-height: 40px
    z-index 100
    background-color: #4EE559
    text-align: center
    /*margin-bottom 40px*/
  }

  .fixed-title .back {
    position: absolute
    top 0
    left: 6px
    z-index 60
  }

  .back .icon-back {
    display block
    padding: 10px
    font-size 18px
    color #fff
  }

  .fixed-title .type .icon {
    position: absolute
    left: 50%
    top: 0
    color #fff
    display: block
    font-size 18px
    transform translateX(-50%)
  }

  .icon .icon-video-camera {
    margin-right 10px
    display inline-block
    vertical-align text-bottom
    font-size 23px
  }

  .type .movie-name {
    display: block
    padding-top 11px
    width: 100%
    color #fff
  }

  .movie-detail {
    position fixed
    top: 0
    bottom: 0
    left: 0
    right: 0
    background-color: #555
  }

  .movie-detail .bg-image {
    text-align center
    width: 100%;
    padding: 60px 0 20px 0
    font-size 0
  }

  .bg-image img {
    width: 50%
  }

  .switch {
    padding 0 20px
    background-color: #fff;
    padding-top 20px
  }
</style>



