<!---->
<template>
  <transition name="rank">
    <div class="rankdetail">
      <div class="fixed-title">
      <span class="back" @click="back">
       <i class="icon-back"></i>
      </span>
        <span class="type">
        <span>{{rankType}}</span>
      </span>
      </div>
      <switches :switches="switches"
                :smallFont="smallFont"
                v-if="rankType===' Top250'"
                :currentIndex="currentIndex"
                @switch="switchTab"
                class="switches"
      >
      </switches>
      <scroll class="rank-list-wrapper" ref="scroll"
              v-for="(item,index) in switches"
              :data=rankList[index]
              v-show="currentIndex===index"
              :pullup="pullup"
              :key="index"
              :probeType="probeType"
              @scrollToEnd="loadMore"
              @scroll="scroll"
      >
        <div class="rank-content" :class="{'more-padding':rankType===' Top250'}">
          <ranklist :rankItems="rankList[index]"
                    :page="index"
                    :hasMore="hasMore[index]"
                    @select="selectMovie"
          ></ranklist>
        </div>
        <loading :fullScreen="fullScreen" v-if="!rankList[index].length"></loading>
      </scroll>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import switches from 'base/switches/switches.vue'
  import scroll from 'base/scroll/scroll.vue'
  import loading from 'base/loading/loading.vue'
  import {top250Rank, weeklyRank, usBoxRank, newMoviesRank} from 'api/movierank'
  import {createRankList} from 'common/js/movielist'
  import ranklist from 'components/ranklist/ranklist.vue'
  import {mapMutations} from 'vuex'

  const SEACH_MORE = 10

  export default {
    data() {
      return {
        rankType: '',
        dataFn: null,
        top250Index: 0,
        rankList: {},
        fullScreen: true,
        currentIndex: 0,
        switches: [
          {name: '1-50'},
          {name: '51-100'},
          {name: '101-150'},
          {name: '151-200'},
          {name: '201-250'}
        ],
        smallFont: true,                                         // tab
        pullup: true,                                           // 
        hasMore: [false, false, false, false, false],          // top250
        movieIndex: [0, 50, 100, 150, 200],                   // 
        scrollY: [0, 0, 0, 0, 0]                             // 
      }
    },
    created() {
      this.probeType = 3                 // 
      this.listenScroll = true           // 
      this.loadingFlag = true            // 
      this._selectType()
      this._getRankList()
    },
    beforeMount() {                   // render
      // top250
      // dom
      for (let i = 0; i < this.switches.length; i++) {
        this.$set(this.rankList, i, [])
      }
    },
    methods: {
      ...mapMutations({
        setMovie: 'SET_MOVIE'
      }),
      back() {                      // 
        this.$router.back()
      },
      switchTab(index) {                     // tab
        this.currentIndex = index
        this.loadingFlag = true
        this.$refs.scroll.forEach((item) => {               // scroll
          item.scrollTo(0, this.scrollY[index])             // 
          setTimeout(() => {
            item.refresh()
          }, 20)
        })
        if (this.rankList[index].length === 0) {                  // 
          this.hasMore[index] = true
          this.dataFn(this.movieIndex[index], SEACH_MORE).then((res) => {
            this.rankList[index] = createRankList(res.subjects)
          })
        }
      },
      selectMovie(movie) {                   // 
        this.$router.push({
          path: `/moiveshow/${movie.id}`
        })
        this.setMovie(movie)
      },
      _selectType() {                                         // 
        let type = this.$route.params.rankType               // 
        switch (type) {                                     // 
          case 'top250':
            this.dataFn = top250Rank
            this.rankType = ' Top250'
            break
          case 'weekly':
            this.dataFn = weeklyRank
            this.rankType = ''
            break
          case 'newmovie':
            this.dataFn = newMoviesRank
            this.rankType = ''
            break
          case 'usbox':
            this.dataFn = usBoxRank
            this.rankType = ''
            break
        }
      },
      _getRankList() {
        if (this.rankType !== ' Top250') {           // top250
          this.dataFn().then((res) => {
            this.rankList[0] = createRankList(res.subjects)
            // console.log(this.rankList[0])
          })
        } else {
          this.dataFn(this.movieIndex[0], SEACH_MORE).then((res) => {
            this.rankList[0] = createRankList(res.subjects)
            this.hasMore[0] = true
          })
        }
      },
      loadMore() {            // 
        const index = this.currentIndex              // 
        if (!this.loadingFlag) {                     // 
          return
        }
        this.loadingFlag = false                   // 
        if (!this.hasMore[index]) {               // 
          return
        }
        this.movieIndex[index] += SEACH_MORE
        this.dataFn(this.movieIndex[index], SEACH_MORE).then((res) => {
          this.rankList[index] = this.rankList[index].concat(createRankList(res.subjects))
          this.loadingFlag = true
          this._checkMore(res)
        })
      },
      _checkMore(data) {                   // 
        let movies = data.subjects
        let end = 50 * (this.currentIndex + 1)
        if (!movies.length || data.start + data.count >= end) {
          this.hasMore[this.currentIndex] = false
        }
      },
      scroll(pos) {
        this.scrollY[this.currentIndex] = pos.y
      }
    },
    components: {
      switches,
      scroll,
      loading,
      ranklist
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" type="text/stylus" scoped>
  @import "~@/common/stylus/mixin.styl"

  .rankdetail {
    position fixed
    top: 0
    left: 0
    bottom: 0
    right: 0
    background-color: #fff;
    z-index 600
  }

  .rankdetail .switches {
    z-index 999
  }

  .rank-enter-active, .rank-leave-active {
    transition all .3s
  }

  .rank-enter, .rank-leave-to {
    transform translate3d(100%, 0, 0)
  }

  .rankdetail .fixed-title {
    border-bottom-1px(#ccc)
    position fixed
    top: 0
    width: 100%
    height: 50px
    z-index 980
    background-color: #fff;
  }

  .fixed-title .back {
    position absolute
    top: 0px
    left: 6px
    /*z-index 100*/
  }

  .back .icon-back {
    display block
    padding 15px
    font-size 18px
    color #42bd56
  }

  .fixed-title .type {
    line-height: 50px
    font-size 16px
    padding-left 60px
    color #333
  }

  .switches {
    position fixed
    top: 50px
    width: 100%
    height: 40px
    z-index 250
    background-color: #fff
  }

  .rank-list-wrapper {
    z-index 900
    position fixed
    top: 0
    bottom: 0
    left: 0
    right: 0
    background-color: #fff;
  }

  .rank-content {
    padding 50px 15px 70px 15px
  }

  .rank-content.more-padding {
    padding-top: 90px
  }

</style>



