<!---->
<template>
  <div class="suggest-wrapper">
    <div class="to-top" v-show="result.length" @click="toTop">
      <span></span>
    </div>
    <scroll class="suggest"
            :data="result"
            :pullup="pullup"
            @scrollToEnd="searchMore"
            ref="suggest"
            @beforeScroll="listScroll"
            :beforeScroll="beforeScroll"
    >
      <div class="suggest-list clearfix">
        <div class="content-box clearfix">
          <!---->
          <div class="suggest-left">
            <div class="suggest-item" v-for="item in evenData" @click="selectItem(item)">
              <img v-lazy="replaceUrl(item.image)" alt="">
              <div class="item-info">
                <div class="title">{{item.title}}</div>
                <div class="casts">{{item.casts}}</div>
                <star class="star" :size="24" :score="item.rating"></star>
                <span class="rating">{{getRating(item.rating)}}</span>
              </div>
            </div>
          </div>
          <!---->
          <div class="suggest-right">
            <div class="suggest-item" v-for="item in oddData" @click="selectItem(item)">
              <img v-lazy="replaceUrl(item.image)" alt="">
              <div class="item-info">
                <div class="title">{{item.title}}</div>
                <div class="casts">{{item.casts}}</div>
                <star class="star" :size="24" :score="item.rating"></star>
                <span class="rating">{{getRating(item.rating)}}</span>
              </div>
            </div>
          </div>
        </div>
        <loading :hasMore="hasMore" v-if="result.length"></loading>
      </div>
      <div class="no-result" v-if="noResult">...</div>
      <loading :fullScreen="fullScreen" v-if="!result.length && !noResult"></loading>
    </scroll>
    <!-- <tab></tab>-->
  </div>
</template>

<script type="text/ecmascript-6">
  import scroll from 'base/scroll/scroll.vue'
  import loading from 'base/loading/loading.vue'
  import {movieSearch, tagSearch} from 'api/moviesearch.js'
  import {createSearchList} from 'common/js/movielist.js'
  import star from 'base/star/star.vue'
  import {mapMutations} from 'vuex'
  // import tab from 'components/tab/tab.vue'

  const SEARCH_MORE = 10

  export default {
    data() {
      return {
        result: [],
        beforeScroll: true,
        searchIndex: 0,                 // 
        dataFn: null,                  // 
        noResult: false,
        fullScreen: true,            // 
        loadingFlag: true,          // 
        pullup: true,              // 
        hasMore: true,             // 
        oddData: [],
        evenData: []
      }
    },
    props: {
      query: {
        type: String,
        default: ''
      },
      tagSearch: {               // 
        type: Boolean,
        default: false
      }
    },
    created() {
      this._selectType()
      this.search()
    },
    watch: {
      query() {
        this._selectType()
        if (this.query) {            // 
          this.result = []          // 
          this.search()            // 
        }
      }
    },
    methods: {
      ...mapMutations({'setMovie': 'SET_MOVIE'}),
      toTop() {               // 
        this.$refs.suggest.scrollTo(0, 0, 1000)
      },
      search() {
        this.result = []
        this.searchIndex = 0
        this.loadingFlag = true
        this.hasMore = true
        this.dataFn(this.query, this.searchIndex, SEARCH_MORE).then((res) => {
          const queryRet = res.title.split('"')
          if (this.query !== queryRet[1]) {                // 
            return
          }
          if (!res.subjects.length) {      // 
            this.noResult = true
          } else {
            this.noResult = false
          }
          this.result = createSearchList(res.subjects)           // 
          this.result.findIndex((item, index) => {
            if (index % 2 !== 1) {       // 
              this.evenData.push(item)
            } else {
              this.oddData.push(item)
            }
          })
          this._checkMore(res)
          // console.log(this.evenData)
          // console.log(this.oddData.length)
        })
      },
      searchMore() {               // 
        if (!this.loadingFlag) {            // 
          return
        }
        this.loadingFlag = false           // 
        if (!this.hasMore) {             // 
          return
        }
        this.searchIndex += SEARCH_MORE
        this.dataFn(this.query, this.searchIndex, SEARCH_MORE).then((res) => {
          this.result = this.result.concat(createSearchList(res.subjects))
          this.result.findIndex((item, index) => {
            if (index % 2 !== 1) {       // 
              this.evenData.push(item)
            } else {
              this.oddData.push(item)
            }
          })
          this.loadingFlag = true
          this._checkMore(res)
        })
      },
      getRating(rate) {                 // 
        if (rate === 0) {
          return ''
        }
        return rate + ''
      },
      _checkMore(data) {                // 
        let movie = data.subjects
        if (!movie.length || data.start + data.count >= data.total) {
          this.hasMore = false
        }
      },
      listScroll() {           // 
        this.$emit('listScroll')
      },
      selectItem(item) {
        this.$router.push({
          path: `/moiveshow/${item.id}`
        })
        this.setMovie(item)            // mutation
        this.$emit('select')
      },
      _selectType() {                 // 
        this.dataFn = this.tagSearch ? tagSearch : movieSearch
      },
      replaceUrl(url) {            // ,
        return ('https://images.weserv.nl/?url=' + url.replace(/http\w{0,1}:\/\//, ''))
      }
    },
    components: {
      scroll,
      loading,
      star
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" type="text/stylus" scoped>
  @import "~@/common/stylus/mixin.styl"

  .suggest-wrapper {
    height: 100%
    position: relative
  }

  .suggest-wrapper .to-top {
    box-sizing border-box
    height: 22px
    position: fixed
    right: 0
    top: 80%
    z-index: 900
    padding 5px
    background-color: #42bd56;
    color #fff
    text-align: center
    font-size 12px
  }

  .suggest {
    height: 100%
    overflow: hidden
  }

  .suggest .suggest-list {
    padding 15px 15px 75px 15px
  }

  .suggest .content-box {
    width: 100%
    padding-bottom 20px
  }

  .content-box .suggest-left {
    width: 48%
    float left
  }

  .content-box .suggest-right {
    width: 48%
    float right
  }

  .suggest-list .suggest-item {
    font-size 0
    width: 100%
    background-color: #f5f5f5
    margin-bottom 20px
  }

  .suggest-list .suggest-item img {
    width: 100%
    height: auto
  }

  .item-info .star {
    display inline-block
  }

  .suggest-item .title {
    width: 80%
    margin 0 auto
    font-size 15px
    color #333
    text-align center
    no-wrap()
  }

  .suggest-item .casts {
    width: 100%
    font-size 14px
    color #333
    text-align center
    no-wrap()
    margin 3px 0
  }

  .suggest .rating {
    margin-left 5px
  }

  .suggest-item .item-info {
    padding 8px 0
    text-align center
  }

  .suggest-item span {
    font-size 12px
  }

  .no-result {
    position: absolute
    top: 50%
    width: 100%
    text-align: center
    font-size 16px
    color #3CB611
  }
</style>



