<!---->
<template>
  <div class="movielist">
    <ul>
      <li ref="group" v-for="(item,index) in moviehot" @click="selectItem(item)">
        <div class="date" v-if="timeData && !dateEqual(index)">{{item.date}}</div>
        <div class="item">
          <div class="info-img">
            <img v-lazy="replaceUrl(item.image)" alt="" height="120" width="80">
          </div>
          <div class="info-desc">
            <p class="title">{{item.title}}</p>
            <star :showScore="showScore" :size="24" :score="item.rating" class="stars"></star>
            <div class="director">{{item.director}}</div>
            <div class="casts">{{item.casts}}</div>
            <div class="hasWatched">{{item.collectCount}} </div>
          </div>
        </div>
      </li>
    </ul>
    <loading :hasMore="hasMore" v-if="moviehot.length"></loading>
  </div>
</template>

<script type="text/ecmascript-6">
  import loading from 'base/loading/loading.vue'
  import star from 'base/star/star.vue'

  export default {
    data() {
      return {
        showScore: true,
        casts: []
      }
    },
    props: {
      moviehot: {
        type: Array,
        default: []
      },
      hasMore: {
        type: Boolean,
        default: true
      },
      timeData: {
        type: Boolean,
        default: false
      }
    },
    created() {
      this.listHeight = []
      this.indexMap = {}
    },
    watch: {
      moviehot() {
        if (this.timeData) {
          setTimeout(() => {           // dom
            this.getMap()
            this._recalculateHeight()
          }, 20)
        }
      }
    },
    methods: {
      selectItem(item) {               // 
        this.$emit('select', item)
      },
      dateEqual(index) {          // 
        if (index === 0) {
          return false
        }
        return this.moviehot[index].date === this.moviehot[index - 1].date
      },
      recalculate() {                  // 
        setTimeout(() => {            // dom
          this.getMap()
          this._recalculateHeight()
        }, 20)
      },
      getMap() {        // 
        let movielists = {}
        for (let i = 0; i < this.moviehot.length; i++) {
          if (movielists[this.moviehot[i].date]) {
            movielists[this.moviehot[i].date].push(i)
          } else {
            movielists[this.moviehot[i].date] = [i]
          }
        }
        this.indexMap = movielists
        // console.log(this.indexMap)
      },
      _recalculateHeight() {       // 
        this.listHeight = []
        const list = this.$refs.group
        let height = 0
        let map = Object.values(this.indexMap)
        // console.log(map)
        this.listHeight.push(height)
        map.forEach((item, index) => {
          item.forEach((item) => {
            height += list[item].clientHeight
          })
          this.listHeight.push(height)
        })
        this.$emit('getHeight', this.listHeight)
        this.$emit('getMap', Object.keys(this.indexMap))
        // console.log(Object.keys(this.indexMap))
      },
      replaceUrl(url) {            // ,
        return ('https://images.weserv.nl/?url=' + url.replace(/http\w{0,1}:\/\//, ''))
      }
    },
    components: {
      loading,
      star
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" type="text/stylus" scoped>
  @import "~@/common/stylus/mixin.styl"
  .movielist ul .date {
    width: 100%
    background-color: #eee
    padding-left 5px
    height: 30px
    line-height: 30px
  }

  .movielist ul .item {
    display: flex
    align-items center
    box-sizing border-box
    padding 15px 0
  }

  .item .info-img {
    flex 80px 0 0
    margin-right 10px
  }

  .item .info-desc {
    height: 120px
    flex 1
    display: flex
    flex-direction column
    justify-content space-around
    overflow: hidden
  }

  .info-desc .title {
    font-size 16px
    color #333
    no-wrap()
  }

  .info-desc .director {
    font-size 12px
  }

  .info-desc .casts {
    font-size 12px
    no-wrap()
  }

  .info-desc .hasWatched {
    color #333
    font-size 12px
  }

  .info-desc .stars {
    display: inline-block
  }
</style>



